    background-size: contain;
    min-height: 700px;
}

.t5.one-column img,
.t5.one-column .img-fluid {
    width: 100%;
    max-width: 75ch;
    margin: 2rem 0 1rem;
}

.t5.one-column article > section {
    padding: 5em 0 2em 0;
    background-size: 75%;
}

.t5 section em,
.t5 section i,
.t4 article i,
.t4 article em {
